@import "~normalize.css/normalize.css";
*, *:before, *:after {
  box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scroll-behavior: smooth;
}

body {
  font-weight: 400;
  font-size: 14px;
  scroll-behavior: smooth;
  background-color: #F8F8FB;
}

img {
  height: auto;
}

h2 {
  font-size: 30px;
  margin: 0 0 30px;
}

p {
  margin: 0;
}
p span {
  color: #00a32a;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  padding-top: 25px;
}

.content {
  width: 80%;
}
.content__post {
  display: flex;
  background-color: #fff;
  box-shadow: 0px 12px 15px rgba(8, 30, 52, 0.3);
  margin: 0 20px 15px 0;
  border-radius: 20px;
  border: 1px solid #b5bcc2;
}
.content__post-right {
  display: flex;
  flex-direction: column;
  padding: 15px;
}
.content__post-img img {
  width: 300px;
  height: 200px;
  border-radius: 20px;
  margin: 15px;
}

.sidebar {
  background-color: #fff;
  width: 20%;
  box-shadow: 0px 12px 15px rgba(8, 30, 52, 0.3);
  margin-bottom: 20px;
  border-radius: 20px;
  border: 1px solid #b5bcc2;
  padding: 15px;
}

.category {
  background-color: #fff;
  box-shadow: 0px 12px 15px rgba(8, 30, 52, 0.3);
  margin: 0 20px 15px 0;
  border-radius: 20px;
  border: 1px solid #b5bcc2;
  padding: 15px;
}
.category li {
  list-style-image: none;
  list-style-type: none;
  margin-right: 25px;
}
.category ul {
  display: inline-flex;
}